@use "sass:math"

$columns: 12
$atom: math.div(100%, $columns)

$break_lg: 1199px
$break_md: 991px
$break_sm: 767px
$break_xs: 479px
$break_xxs: 321px

$offset: 20px
$offset_lg: $offset
$offset_md: $offset
$offset_sm: $offset
$offset_xs: $offset
$offset_xxs: $offset

$offset_one_side: ($offset * 0.5)
$offset_lg_one_side: ($offset_lg * 0.5)
$offset_md_one_side: ($offset_md * 0.5)
$offset_sm_one_side: ($offset_sm * 0.5)
$offset_xs_one_side: ($offset_xs * 0.5)
$offset_xxs_one_side: ($offset_xxs * 0.5)

$fields: 20px
$fields_sm: 15px

=wrapper-full()
	padding-left: $fields
	padding-right: $fields

	+sm-block()
		padding-left: $fields_sm
		padding-right: $fields_sm

=row-offsets()
	margin-left: ($offset_one_side * -1)
	margin-right: ($offset_one_side * -1)

=col-offsets($type)
	#{$type}-left: $offset_one_side
	#{$type}-right: $offset_one_side

=wrapper()
	max-width: 1256px
	margin: 0 auto
	+wrapper-full()

=row-flex()
	display: flex
	flex-wrap: wrap
	+row-offsets()

=row-float()
	+row-offsets()
	+clearfix()

=row-ib()
	+row-offsets()

=col()
	box-sizing: border-box
	word-wrap: break-word
	+col-offsets(margin)

=col-float()
	float: left

=col-ib()
	display: inline-block
	vertical-align: top

=col-padding()
	word-wrap: break-word
	+col-offsets(padding)

=size($n)
	$val: math.div(100%, $columns) * $n
	width: #{calc(#{$val} - #{$offset})}

=size-lg($n)
	$val: math.div(100%, $columns) * $n
	+lg(width, #{calc(#{$val} - #{$offset_lg})})

=size-md($n)
	$val: math.div(100%, $columns) * $n
	+md(width, #{calc(#{$val} - #{$offset_md})})

=size-sm($n)
	$val: math.div(100%, $columns) * $n
	+sm(width, #{calc(#{$val} - #{$offset_sm})})

=size-xs($n)
	$val: math.div(100%, $columns) * $n
	+xs(width, #{calc(#{$val} - #{$offset_xs})})

=size-xxs($n)
	$val: math.div(100%, $columns) * $n
	+xxs(width, #{calc(#{$val} - #{$offset_xxs})})

=shift($n)
	$val: math.div(100%, $columns) * $n
	margin-left: #{calc(#{$val} + #{$offset_one_side})}
	margin-right: #{calc(#{$val} + #{$offset_one_side})}

=shift-left($n)
	$val: math.div(100%, $columns) * $n
	margin-left: #{calc(#{$val} + #{$offset_one_side})}

=shift-right($n)
	$val: math.div(100%, $columns) * $n
	margin-right: #{calc(#{$val} + #{$offset_one_side})}

=shift-padding($n)
	$val: math.div(100%, $columns) * $n
	padding-left: #{calc(#{$val} + #{$offset_one_side})}
	padding-right: #{calc(#{$val} + #{$offset_one_side})}

=shift-padding-left($n)
	$val: math.div(100%, $columns) * $n
	padding-left: #{calc(#{$val} + #{$offset_one_side})}

=shift-padding-right($n)
	$val: math.div(100%, $columns) * $n
	padding-right: #{calc(#{$val} + #{$offset_one_side})}

=shift-lg($n)
	$val: math.div(100%, $columns) * $n
	+lg(margin-left, #{calc(#{$val} + #{$offset_lg_one_side})})
	+lg(margin-right, #{calc(#{$val} + #{$offset_lg_one_side})})

=shift-lg-left($n)
	$val: math.div(100%, $columns) * $n
	+lg(margin-left, #{calc(#{$val} + #{$offset_lg_one_side})})

=shift-lg-right($n)
